class MetaData {
  constructor(_object) {
    this.import(_object);
  }

  // Setters
  set variable(_v) {
    this._variable = _v;
  }

  // Getters
  get variable() {
    return this._variable;
  }

  set seed(_s) {
    this._seed = _s;
  }

  get seed() {
    return this._seed;
  }

  set default(_d) {
    this._default = _d;
  }

  get default() {
    return this._default;
  }

  set stored(_stored) {
    this._stored = _stored;
  }

  get stored() {
    return this._stored;
  }

  set name(_n) {
    this._name = _n;
  }

  get name() {
    return this._name;
  }



  // Read With Type Casting
  get byInteger() {
    return parseInt(this.variable);
  }

  get byFloat() {
    return parseFloat(this.variable);
  }

  get byNumber() {
    return Number(this.variable);
  }

  //   string  
  get byString() {
    return String(this.variable);
  }

  get byBoolean() {
    if (this.variable === 'true') {
      return true;
    } else if (this.variable === 'false') {
      return false;
    } else {
      throw new Error("Boolean     .");
    }
  }

  get byObject() {
    try {
      return JSON.parse(this.variable);
    } catch (_e) {
      throw new Error(`Fail Parse JSON. target : '${this.variable}', Native error message:${_e.message}`);
    }
  }





  // Read With Type Casting
  set fromInteger(_v) {
    this._variable = String(_v);
  }

  // Read With Type Casting
  set fromFloat(_v) {
    this._variable = String(_v);
  }

  set fromNumber(_v) {
    this._variable = String(_v);
  }

  // Read With Type Casting
  set fromString(_v) {
    this._variable = _v;
  }

  // Read With Type Casting
  set fromBoolean(_v) {
    if (_v === true) {
      this._variable = 'true';
    } else if (_v === false) {
      this._variable = 'false';
    } else {
      throw new Error("Boolean    . boolean  .");
    }
  }

  // Read With Type Casting
  set fromObject(_v) {
    this._variable = JSON.stringify(_v);
  }

  reset() {
    this.variable = this.seed;
  }

  save() {
    this.stored = this.variable;
  }

  load() {
    this.variable = this.stored;
  }

  import (_data) {
    if (typeof _data === 'object') {
      this.variable = _data.seed; // variable       .
      this.seed = _data.seed; // Seed     .
      this.default = _data.default; // default  seed      .
      this.name = _data.name;
    } else {
      // object       .
      this.seed = this.variable = _data;
    }
  }

  export () {
    let exportO = {
      seed: this.seed,
      default: this.default
    };

    if (this.name) {
      exportO.name = this.name;
    }

    return exportO;
  }
}

export default MetaData;


